
// 防抖
export class Debounced {
    
    private timeoutId: number | null = null;

    public use(callback: () => void, delay: number = 500): void {
        return (() => {
            this.timeoutId !== null && clearTimeout(this.timeoutId);
            this.timeoutId = setTimeout(() => {
                callback.apply(this);
            }, delay);
        })();
    }
}